<!-- 个人订阅 -->
    <template>
        <div id="GeRenDingYue">
            <v-headr :myname="myname"></v-headr>
            <div class="bod-list-container">
                <ul class="list-warp" v-for="(item,key) in DingYueShuJu">
                     <mt-cell-swipe
                          :right="[
                            {
                              content: '删除',
                              style: { background: 'red', color: '#fff' },
                              handler: function(){ delVideo(key)}
                            }
                          ]">


                            <li class="list-con clearfix">
                                
                                <div class="list-num clearfix ">
                                    <!-- <span class="list-num-percentage float-lft">{{item.percentage}}<i class="symbol-percentage">{{item.date}}</i></span> -->

                                    <p class="list-title-text">{{item.title}}</p>
                                    <span class="list-num-money float-rigt"><i class="symbol-money">￥</i> {{item.price}}</span>
                                </div>
                           
                             </li>

                        </mt-cell-swipe>
                    
                    
                </ul>
                
                
            </div>
            
        </div>
    </template>

    <script type="text/javascript">
    import Headr from '../../components/header.vue' 
     const DingYueJS = JSON.parse(sessionStorage.dingyue);
    console.log(DingYueJS.result[0].JiShi)
    export default {
        name:'GeRenDingYue',
        data(){
            return {
                myname:{
                    name:'个人订阅',
                    IsSubPage:true
                },
                DingYueShuJu:null

                
                
            }
        },
        components:{
            'v-headr':Headr,
        },
        methods:{
            getDate(){
                this.DingYueShuJu = DingYueJS.result[0].JiShi
                console.log(this.DingYueShuJu)
            },
           delVideo(key) {
                console.log(key)
                /*此处应该是吧对应的一条数据删了就ok*/

                this.$toast({
                  message: '删除成功',
                  duration: 2000
                });
               this.getDate()
             
            },
            
           


        },
        mounted(){
            this.getDate()
            //this.$indicator.open('加载中...');
            //this.$messagebox('delete')


        }

    }
</script>
<style type="text/css">
    .mint-cell-title {
            -webkit-box-flex: 0;
